import React from 'react';
import db from '../data.json';

const Main = () => {
  const joiningCriteria = [
    'People between the ages of 18 and 35 years.',
    'A demonstrated passion for coding and technology.',
    'The time and capacity to commit to a full coding bootcamp. Classes are three times per week in person at one of our learning hubs.',
    'An intermediate level of English comprehension.',
    'The aptitude to succeed in the selection process.',
  ];

  let awesome = 0;
  
  function showAwesome(){
    if(awesome === 0){
      document.getElementById("awesome").style.display = "flex";
      awesome = 1;
    }else{
      document.getElementById("awesome").style.display = "none";
      awesome = 0;
    }
  }

  function showData(){
    //using basics of JS
    // let countries = "";
    // let bootcamps = "";
    // for(let i=0;i<db.countries.length;i++){
    //   countries += db.countries[i]+",";
    // }
    // for(let i=0;i<db.bootcamps.length;i++){
    //   bootcamps += db.bootcamps[i]+",";
    // }
    // document.getElementById("dataDisplay").innerHTML = "<p>Countries Re:Coded works in: "+countries+".</p><p>Re:Coded Bootcamps: "+bootcamps+".</p>";
    //or using join method 
    document.getElementById("dataDisplay").innerHTML = "<p>Countries Re:Coded works in: "+db.countries.join(",")+".</p><p>Re:Coded Bootcamps: "+db.bootcamps.join(",")+".</p>";
  }

  function campCountries(){
    let campsKeys = Object.keys(db.camps);
    return(campsKeys.map((e,i) => (
      <option 
        value={e[0].toUpperCase()+e.slice(1)} 
        key={i} 
        className="camp-country-option" />)
    ));
  }
  
  function showCamps(e){
    e.preventDefault();
    let camp = document.getElementById("searchBox").value.toLowerCase();
    //to check whether it exist in camps or not
    let searchResults = document.getElementById("searchResults");
    if(db.camps[camp] !== undefined){
      let campsList = "";
      for(let i=0;i<db.camps[camp].length;i++){
        campsList += "<p>"+db.camps[camp][i]+"</p>";
      }
      searchResults.innerHTML = campsList;
    }else{
      searchResults.innerHTML = "<p>country not found</p>";
    }
  }

  return (
    <section className="bootcamp">
      {/* Where should the following h2 tag be displayed? Center or right? and why? */}
      {/* Center because it aligned to the center of the page as shown in photo */}
      <h2 className="align-right" style={{ textAlign: 'center' }}>
        Re:Coded Front-End Web Development Bootcamp
      </h2>
      <h3>What is our bootcamp</h3>
      {/* Make every 'Front-End Web Development' sentence in the following article bold */}
      <p>
        Our <b>Front-End Web Development</b> Bootcamp is designed to kickstart your
        career as junior web developer or as an entrepreneur with a vision to
        build your own website, app or software solution.
      </p>
      <p>
        As a non-profit organization we have partnered with Flatiron School in
        New York, one of the leading coding schools in the world. That allows us
        to teach their <b>Front-End Web Development</b> course introducing you to HTML
        and CSS. We provide additional training on React, a library for building
        user interfaces maintained by Facebook and Instagram. And during our
        program, you will learn JavaScript, all of which will allow you to build
        amazing websites and accelerate your journey to becoming a working Web
        Developer!
      </p>
      <p>
        By the end of the program, you will have a portfolio of completed
        projects to highlight your achievements.
      </p>
      <p>
        The <b>Front-End Web Development</b> course will take approximately 3.5 months
        with 10 hours per week of in-person training and 15-25 hours per week of
        self-study.
      </p>
      <p>
        The last 1.5 month of the bootcamp will be dedicated to a final project
        to help you build your portfolio of work.
      </p>
      <p>Our programs are all in english.</p>
      <h3>Who can join</h3>
      {/* What are the differences between <ul> and <ol>? */}
      {/* 
        Unordered List (ul):
        1- list item (li) inside ul tag preceded by a symbol e.g. black dot
        2- used to show elements in unordered way
        Ordered List (ol):
        1- list item (li) inside ol tag preceded by number or letter e.g. 1-
        2- used to show elements in ordered way
        note: with both cases we can specify what we want to display before the list item
      */}
      <ul>
        {joiningCriteria.map((e,i) => (<li key={i}>{e}</li>))}
      </ul>
      <div className="buttons">
        {/* Using CSS, try to provide multiple methods to display these divs in a row like the attached pictures. Keep one of the methods, and comment out the rest */}
        <div className="infoDiv">
          {/* When clicking "More Info" button, get the countries and bootcamps data from the JSON file and display them in the '#dataDisplay' div. */}
          <button 
            id="infoBtn"
            onClick={showData}>More Info</button>
          <div id="dataDisplay"></div>
        </div>
        <div className="awesomeDiv">
          <button 
            id="awesomeBtn" 
            onClick={showAwesome}>I'm Awesome</button>
          {/*  When clicking the "I'm Awesome" button, display the content of '#awesome' div. When clicking it again, hide the content of '#awesome' div. */}
          <div id="awesome">
            <p>
              <em>I'm awesome, I'm doing the js part</em>
            </p>
          </div>
        </div>
        <div className="searchDiv">
          <form id="search">
            <input
              type="text"
              name="searchBox"
              id="searchBox"
              placeholder="search bootcamps"
              list="camp-country"
            />
            <datalist id="camp-country">
              {campCountries()}
            </datalist>
            {/* When clicking the "search" button, search the camps in JSON file by country and display the search results in the '#searchResults' div. */}
            <input type="submit" id="searchBtn" value="search" onClick={showCamps} />
          </form>
          <div id="searchResults"></div>
        </div>
      </div>
      {/* ##Delete the wrong fact/facts in the comments below */}
      {/* 2. The same class can be used multiple times, but the same id can be used only once */}
      {/* 3. In CSS, '#' refers to id while '.' refers to class */}
    </section>
  );
};

export default Main;
